@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/ptmonoregular.woff2") format("woff2"), url("../fonts/ptmonoregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensedregular.woff2") format("woff2"), url("../fonts/robotocondensedregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensedbold.woff2") format("woff2"), url("../fonts/robotocondensedbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "PT Mono", "Courier", monospace;
  color: #041827;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f8fdff;
}

a {
  text-decoration: none;
}
a:focus {
  outline: 2px solid #4c97b7;
}
a:hover, a:focus {
  opacity: 0.8;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f2f5f6;
  -webkit-text-fill-color: #041827;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  max-width: 1320px;
  margin: 0 auto;
  padding-left: 50px;
  padding-right: 50px;
}
@media (max-width: 1023px) {
  .container {
    width: 100%;
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #f2f5f6;
  text-transform: uppercase;
  cursor: pointer;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  border-radius: 3px;
  border: none;
  user-select: none;
}
.btn:focus {
  outline: 2px solid #4c97b7;
}
.btn:hover, .btn:focus {
  opacity: 0.8;
}
.btn:disabled {
  opacity: 0.5;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(4, 24, 39, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  width: 100%;
  padding: 40px;
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 500px;
  padding: 40px;
  margin-left: -15px;
  top: 106px;
  background-color: #00b2ff;
}
.modal__close-button {
  position: absolute;
  top: 16px;
  right: 16px;
  z-index: 1;
  width: 20px;
  height: 20px;
  padding: 20px;
  background-color: transparent;
  background-image: url("/img/svg/close-button.svg");
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal__form {
  display: flex;
  flex-direction: column;
}

.modal__form h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 30px;
  line-height: 42px;
  font-weight: 400;
  color: #ffffff;
  margin-top: -3px;
  margin-bottom: 4px;
}

.modal__form p {
  font-family: "PT Mono", "Courier", monospace;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  margin-top: 0;
  margin-bottom: 37px;
}

.modal__form input {
  border: none;
  background-color: rgba(4, 20, 39, 0.15);
  border-radius: 3px;
  padding: 15px;
  color: #ffffff;
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 20px;
  box-sizing: border-box;
}
.modal__form input::placeholder {
  color: #ffffff;
}
.modal__form input:hover, .modal__form input:focus {
  outline: 1px solid #041827;
}

.modal__form textarea {
  border: none;
  background-color: rgba(4, 20, 39, 0.15);
  border-radius: 3px;
  padding: 25px 15px;
  color: #ffffff;
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 22px;
  height: 155px;
}
.modal__form textarea::placeholder {
  color: #ffffff;
}
.modal__form textarea:hover, .modal__form textarea:focus {
  outline: 1px solid #041827;
}

.modal__form button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #041827;
  border: 0;
  border-radius: 3px;
  padding: 15px 60px;
  padding-bottom: 13px;
  margin-bottom: 20px;
  cursor: pointer;
  user-select: none;
}
.modal__form button:hover {
  background-color: rgba(4, 24, 39, 0.7);
}

.modal__form label {
  display: flex;
  align-items: center;
  margin-top: auto;
  margin-left: 8px;
  cursor: pointer;
  order: 1;
}

.modal__form span:last-of-type {
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.8;
}

.modal__checkbox-mark {
  display: block;
  width: 15px;
  height: 15px;
  border: 1px solid #ffffff;
  margin-right: 11px;
  position: relative;
  border-radius: 2px;
}

.modal__form input[type=checkbox]:checked ~ .modal__checkbox-mark::before {
  position: absolute;
  content: "";
  left: 0;
  top: 6px;
  width: 4px;
  height: 1px;
  background: #ffffff;
  transform: rotate(50deg);
}

.modal__form input[type=checkbox]:checked ~ .modal__checkbox-mark::after {
  content: "";
  position: absolute;
  left: 3px;
  top: 6px;
  width: 7px;
  height: 1px;
  background: #ffffff;
  transform: rotate(-50deg);
}

.modal__form input[type=checkbox]:hover ~ span {
  color: #041827;
}

.modal__form input[type=checkbox]:hover + .modal__checkbox-mark,
.modal__form input[type=checkbox]:focus + .modal__checkbox-mark {
  border-color: #041827;
}

.modal__form input[type=checkbox]:checked:hover ~ .modal__checkbox-mark::before,
.modal__form input[type=checkbox]:checked:hover ~ .modal__checkbox-mark::after,
.modal__form input[type=checkbox]:checked:focus ~ .modal__checkbox-mark::before,
.modal__form input[type=checkbox]:checked:focus ~ .modal__checkbox-mark::after {
  background-color: #041827;
}

.modal__logo {
  display: block;
  background-color: #041827;
  padding: 10px;
  margin-bottom: 15px;
}

.header {
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  position: relative;
  z-index: 10;
}

.header--fixed {
  position: fixed;
  top: 0;
  width: 100%;
}
@media (max-width: 1023px) {
  .header--fixed {
    position: relative;
  }
}

.header__navigation {
  padding-top: 28px;
  padding-bottom: 35px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 5%;
}
@media (max-width: 1023px) {
  .header__navigation {
    padding-top: 20px;
    padding-bottom: 25px;
    padding-right: 9%;
    align-items: flex-start;
  }
}
@media (max-width: 767px) {
  .header__navigation {
    padding-top: 10px;
    padding-bottom: 20px;
    padding-right: 11%;
  }
}

.header__logo {
  display: block;
  margin-right: 12px;
  align-self: start;
}
@media (max-width: 1023px) {
  .header__logo {
    margin-right: auto;
  }
}
@media (max-width: 767px) {
  .header__logo {
    margin-top: 5px;
  }
}

.header__logo img {
  width: 215px;
  height: 55px;
}
@media (max-width: 767px) {
  .header__logo img {
    width: 35px;
    height: 35px;
  }
}

.header__contacts-list {
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 45px;
  row-gap: 10px;
  margin-left: -20px;
}
@media (max-width: 1023px) {
  .header__contacts-list {
    margin: 0;
    column-gap: 20px;
  }
}

.header__contacts-link {
  color: #f2f5f6;
  display: flex;
  width: fit-content;
}
@media (max-width: 1023px) {
  .header__contacts-link {
    padding: 10px;
  }
}
.header__contacts-link:hover {
  color: #00b2ff;
}

.header__link-text {
  margin-left: 15px;
}
@media (max-width: 1023px) {
  .header__link-text {
    display: none;
  }
}

.header__contacts-link span {
  margin-left: 15px;
}
@media (max-width: 1023px) {
  .header__contacts-link span {
    display: none;
  }
}

.header__button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #f2f5f6;
  background-color: transparent;
  padding: 10px 15px;
  border: 1px solid #f2f5f6;
  border-radius: 3px;
  cursor: pointer;
  margin-top: 7px;
  margin-right: 15px;
  user-select: none;
  height: fit-content;
  align-self: start;
}
.header__button:hover {
  color: #00b2ff;
  border-color: #00b2ff;
}
@media (max-width: 1023px) {
  .header__button {
    display: none;
  }
}

.footer {
  background-color: #041827;
  padding-top: 58px;
  padding-bottom: 45px;
  flex-shrink: 0;
}
@media (max-width: 1023px) {
  .footer {
    padding-top: 55px;
    padding-bottom: 55px;
  }
}
@media (max-width: 767px) {
  .footer {
    padding-top: 0;
    padding-bottom: 35px;
  }
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 1023px) {
  .footer__wrapper {
    flex-direction: column;
  }
}
@media (max-width: 767px) {
  .footer__wrapper {
    padding: 0;
  }
}

.footer__logo {
  display: block;
  margin-bottom: 30px;
}
@media (max-width: 1023px) {
  .footer__logo {
    margin-top: -5px;
    margin-bottom: 0;
  }
}
@media (max-width: 767px) {
  .footer__logo {
    margin: 0 auto 15px auto;
  }
}

.footer__nav {
  display: grid;
  grid-template-columns: auto minmax(200px, 345px);
  gap: 30px;
  margin-left: auto;
}
@media (max-width: 1023px) {
  .footer__nav {
    margin-left: 0;
    column-gap: 105px;
  }
}
@media (max-width: 767px) {
  .footer__nav {
    display: flex;
    flex-direction: column;
    gap: 0;
  }
}

.footer__nav button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: transparent;
  margin-bottom: 20px;
  border: 0;
}
@media (max-width: 1023px) {
  .footer__nav button {
    margin-bottom: 22px;
  }
}
@media (max-width: 767px) {
  .footer__nav button {
    width: 100%;
    margin-bottom: 0;
    text-align: left;
  }
}

@media (max-width: 767px) {
  .footer__nav .is-open {
    margin-bottom: 16px;
  }
}

.footer__nav-list {
  display: grid;
  grid-template-columns: minmax(160px, 220px) minmax(160px, 220px);
  column-gap: 30px;
}
@media (max-width: 1023px) {
  .footer__nav-list {
    display: flex;
    flex-direction: column;
  }
}

.footer__nav ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.footer__nav li,
.footer__nav a {
  color: rgba(255, 255, 255, 0.7);
}
@media (max-width: 767px) {
  .footer__nav li,
.footer__nav a {
    font-size: 14px;
    line-height: 25px;
  }
}

@media (max-width: 1023px) {
  .footer__nav-list > li {
    margin-bottom: 5px;
  }
}

.footer__nav-first-column li:not(:last-of-type),
.footer__nav-second-column li:not(:last-of-type) {
  margin-bottom: 6px;
}
