  }
}
@media (max-width: 767px) {
  .feedback__form p {
    font-size: 14px;
    line-height: 19px;
    margin-bottom: 32px;
  }
}

.feedback__form input {
  grid-row: 3/4;
  border: none;
  background-color: rgba(4, 20, 39, 0.15);
  border-radius: 3px;
  padding: 15px;
  color: #ffffff;
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 18px;
}
.feedback__form input::placeholder {
  color: #ffffff;
}
.feedback__form input:hover, .feedback__form input:focus {
  outline: 1px solid #041827;
}

.feedback__form textarea {
  grid-column: 1/-1;
  grid-row: 4/5;
  border: none;
  background-color: rgba(4, 20, 39, 0.15);
  border-radius: 3px;
  padding: 23px 15px 20px;
  color: #ffffff;
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 35px;
  height: 155px;
}
.feedback__form textarea::placeholder {
  color: #ffffff;
}
.feedback__form textarea:hover, .feedback__form textarea:focus {
  outline: 1px solid #041827;
}
@media (max-width: 1023px) {
  .feedback__form textarea {
    padding: 32px 15px;
    margin-bottom: 37px;
  }
}
@media (max-width: 767px) {
  .feedback__form textarea {
    height: 180px;
    margin-bottom: 30px;
    padding: 24px 15px;
  }
}

.feedback__form button {
  grid-column: 1/2;
  grid-row: 5/6;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #041827;
  border: 0;
  border-radius: 3px;
  padding: 15px 25px 11px 25px;
  cursor: pointer;
  width: 265px;
  user-select: none;
}
.feedback__form button:hover {
  background-color: rgba(4, 24, 39, 0.7);
}
@media (max-width: 767px) {
  .feedback__form button {
    width: 100%;
    margin-bottom: 19px;
  }
}

.feedback__form label {
  grid-column: 2/3;
  grid-row: 5/6;
  display: flex;
  align-items: center;
  margin-top: 14px;
  margin-left: 8px;
  cursor: pointer;
}
@media (max-width: 1023px) {
  .feedback__form label {
    margin-left: 0;
    margin-top: 5px;
  }
}
@media (max-width: 767px) {
  .feedback__form label {
    margin-left: 6px;
    order: 1;
  }
}

.feedback__form span:last-of-type {
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.8;
}

.feedback__checkbox-mark {
  display: block;
  width: 20px;
  height: 15px;
  border: 1px solid #ffffff;
  margin-right: 11px;
  position: relative;
  border-radius: 2px;
}
@media (max-width: 1023px) {
  .feedback__checkbox-mark {
    width: 15px;
    height: 15px;
  }
}
@media (max-width: 767px) {
  .feedback__checkbox-mark {
    width: 20px;
    height: 15px;
  }
}

.feedback__form input[type=checkbox]:checked ~ .feedback__checkbox-mark::before {
  position: absolute;
  content: "";
  left: 2px;
  top: 7px;
  width: 4px;
  height: 1px;
  background: #ffffff;
  transform: rotate(50deg);
}

.feedback__form input[type=checkbox]:checked ~ .feedback__checkbox-mark::after {
  content: "";
  position: absolute;
  left: 4px;
  top: 6px;
  width: 7px;
  height: 1px;
  background: #ffffff;
  transform: rotate(-50deg);
}

.feedback__form input[type=checkbox]:hover ~ span {
  color: #041827;
}

.feedback__form input[type=checkbox]:hover + .feedback__checkbox-mark,
.feedback__form input[type=checkbox]:focus + .feedback__checkbox-mark {
  border-color: #041827;
}

.feedback__form input[type=checkbox]:checked:hover ~ .feedback__checkbox-mark::before,
.feedback__form input[type=checkbox]:checked:hover ~ .feedback__checkbox-mark::after,
.feedback__form input[type=checkbox]:checked:focus ~ .feedback__checkbox-mark::before,
.feedback__form input[type=checkbox]:checked:focus ~ .feedback__checkbox-mark::after {
  background-color: #041827;
}

@media (max-width: 767px) {
  .no-js ul {
    display: flex;
    flex-direction: column;
  }
}

@media (max-width: 767px) {
  .no-js button::before,
.no-js button::after {
    display: none;
  }
}

@media (max-width: 767px) {
  .is-open + ul {
    display: flex;
    flex-direction: column;
  }
}

